* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
}

.App {
  text-align: center;
}

header {
  font-size: calc(10px + 2vmin);
  margin-top: 2rem;
}

main {
  width: 600px;
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.maze {
  margin: 0 auto;
}

table,
th,
td {
  border: 1px solid red;
}

td {
  width: 3rem;
  height: 3rem;
  background-color: #fff;
}

.wall {
  background: #000;
  color: black;
}

.path {
  background: lightgreen;
  color: lightgreen;
}

h2 {
  margin-top: 1rem;
}

.controls {
  margin: 1rem;
}

button {
  margin: 0 1rem;
  padding: 1rem;
  border-radius: 5px;
}

.maze-size {
  width: 14rem;
  margin: 0 auto;
}
